
.content_wrapper {
  width: 100%;
  height: 600px;
  padding-top: 60px;
  background: #e4ecf3;
}
.content_wrapper .form {
  width: 600px;
  background: #fff;
  margin: 0 auto;
  margin-top: 40px;
  border: 1px solid rgba(0,0,0,0.2);
  box-shadow: 0 0 3px rgba(0,0,0,0.2);
  border-radius: 8px;
}
.content_wrapper .form .reg_item {
  width: 100%;
  height: 100px;
  line-height: 100px;
  position: relative;
}
.content_wrapper .form .reg_item span {
  position: absolute;
  font-size: 18px;
  color: #00a1d6;
  left: 140px;
}
.content_wrapper .form .reg_item input {
  border: 2px solid rgba(0,0,0,0.3);
  text-indent: 4px;
  height: 30px;
  line-height: 30px;
  border-radius: 6px;
  top: 50%;
  margin-top: -15px;
  position: absolute;
  left: 200px;
}
.content_wrapper .form .reg_item .active {
  border: 1px solid #e96900;
}
.content_wrapper .form .reg_item p {
  position: absolute;
  top: 70px;
  height: 16px;
  line-height: 16px;
  font-size: 14px;
  text-indent: 200px;
  color: #999;
}
.content_wrapper .form .submit {
  display: block;
  color: #fff;
  border-radius: 8px;
  border: none;
  width: 120px;
  height: 50px;
  background: #67bdcd;
  line-height: 40px;
  margin: 20px auto 40px auto;
  font-size: 26px;
  letter-spacing: 6px;
  cursor: pointer;
}


